.menu{
    list-style-type: none;
    width:20%;
    background-color: #735a73;
    overflow: hidden;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-pack:center;
        justify-content:center;
    height: 90%;
    float:left;
	position:fixed;
	top:10%;
}

.menu_item{
	text-decoration: none;
    color: azure;
    font-size: 1.1em;
	width:100%;
    text-align: left;
    height: 60px;
    border-bottom: 1px solid #adadad;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align:center;
        align-items:center;
    cursor: pointer;
    -webkit-transition: .5s ;
    -o-transition: .5s ;
    transition: .5s ;
}

.menu_item:hover{
    background-color:#a795a7;
}

.list_menu{
	text-decoration: none;
    list-style-type: none;
    width: 100%;
}

.active{
    background-color:#a795a7;
}

a:active{
    background-color:#a795a7;
}

@media (max-width:866px) {

    .menu{
        display: none;
    }
}


