.h2{
	font-size: 1.8em;
	text-align: center;
	color: #735a73;
	border-bottom: 1px solid #735a73;
}

.scroll{
	width:100%;
	display: -ms-flexbox;
	display: flex;
	padding:10px 10px 5px 10px;
	-ms-flex-pack:justify;
	    justify-content:space-between;
	overflow: auto;
	-ms-flex-direction:column;
	    flex-direction:column;
}

.change{
	font-size:1.2em;
}

.mailingListTable {
	width: 100%;
	margin: 0 auto;
	border-collapse: collapse;
	border: 0 solid transparent;
}

.mailingListTable tr{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.mailingListTable tr :hover{
	cursor:pointer;
	background-color: #a795a7;
}

.mailingListTable tr:nth-child(odd) {
	background-color: #dddddd ;
}

.mailingListTable tr:nth-child(even) {
	background-color:#f1f1f1;
}

.mailingListTable tr td{
	width:100%;
	padding: 3px 0 3px 0;
	cursor:pointer;
}

.mailingListTable td{
	text-align: left;
	padding-left: 5px;
	border: solid 0 transparent;
}

.mailingListTable tr th{
	text-align:left;
	border:none;
}

#ChooseSendRemove{
	background-color: #f1f1f1;
}

.trash{
	-ms-flex-align: right;
	    align-items: right;
	-ms-flex-pack: center;
	    justify-content: center;
	cursor: pointer;
}

.mailListContainer{
	width:100%;
}

.mailingListContactsTable {
	width:100%;
	border-collapse: collapse;
}

.mailingListContactsTable td{
	border-collapse: collapse;
	text-align: left;
	padding: 5px 0 5px 5px;
}

.mailingListContactsTable th{
	text-align: left;
	padding-left: 5px;
	border-bottom: 1px solid #f1f1f1;
}

.mailingListContactsTable thead tr{
	border-bottom:1px solid lightgrey;
}

.mailingListContactsTable tr{
	border-bottom:1px solid lightgrey;
}

#sendIconBtn{
	margin-right: 4%;
}

#trashIconBtn{
	margin-right: 22%;
}

.btnContainer{
	width:100%;
}

.mailBtn{
	height: 37px;
	border: 1px solid #735a73;
	padding: 0 10px;
	margin: 15px 10px 10px 10px;
	font-size: 1em;
	background: #735a73;
	color: #fff;
	-webkit-transition: background-color 0.5s ease;
	-o-transition: background-color 0.5s ease;
	transition: background-color 0.5s ease;
}

.mailBtn:hover{
	background-color:#60c5bb;
}

.mailBtn:disabled{
	color: #735a73;
	background-color: #ddd;
}

#sendBtn:disabled{
	color: #735a73;
	background-color: #ddd;
}


